        <SunnyRow type="flex" justify="center" middle="center" back-color="#fff">
                <image class="logo" :src="require('@/static/imgs/logo.png')"></image>
        </SunnyRow>

        <SunnyList label="布局组件" back-color="#f4f6f7" class="title">
