            <image slot="icon" :src="require('@/static/imgs/index/flex.png')"></image>
            <SunnyIcon slot="append" name="s-icon-arrow-right"/>
        </SunnyList>

        <SunnyList label="基础组件" back-color="#f4f6f7" class="title">
            <image slot="icon" :src="require('@/static/imgs/title.png')"></image>
        </SunnyList>
