            <SunnyIcon slot="append" name="s-icon-arrow-right"/>
        </SunnyList>

    </view>
</template>

<script>
    import SunnyCard from '../../components/SunnyCard/SunnyCard';
    import SunnyList from '../../components/SunnyList/SunnyList';
    import SunnyIcon from '../../components/SunnyIcon/SunnyIcon';
    import SunnyRow from '../../components/SunnyRow/SunnyRow';
    import SunnyCol from '../../components/SunnyCol/Sunnycol';
